@import url(https://fonts.googleapis.com/css?family=Roboto:500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(240, 240, 240);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.WelcomeScreen {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    background: rgb(240, 240, 240);
    -webkit-justify-content: center;
            justify-content: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.login-button {
    color: #494949;
    text-transform: uppercase;
    text-decoration: none;
    background: #ffffff;
    border: 4px solid #494949;
    display: inline-block;
    transition: all 0.4s ease 0s;
    min-width: 10rem;
    margin: 1rem;
}

.google-btn {
    width: 184px;
    height: 42px;
    margin: 1rem;
    background-color: #4285f4;
    border-radius: 2px;
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}

.google-btn:hover {
    box-shadow: 0 0 6px #4285f4;
}

google-btn:active {
    background: #1669f2;
}

.google-icon-wrapper {
    position: absolute;
    margin-top: 1px;
    margin-left: 1px;
    width: 40px;
    height: 40px;
    border-radius: 2px;
    background-color: #fff;
}

.google-icon {
    position: absolute;
    margin-top: 11px;
    margin-left: -8px;
    width: 18px;
    height: 18px;
}

.btn-text {
    float: right;
    color: #fff;
    font-size: 14px;
    letter-spacing: 0.2px;
    font-family: "Roboto";
    background-color: #4285f4;
    width: 80%;
    height: 100%;
    text-align: center;
}
.App {
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}

.App * {
    box-sizing: border-box;
}

ul {
    padding: 0;
    list-style: none;
}

p {
    margin: 5px 0;
}

.display-none {
    display: none;
}

.showSuggestions {
    display: block;
}

input[type="text"], input[type="number"] {
    padding: 8px 6px 8px 10px;
    border-radius: 4px;
    outline: none;
    border: 1px solid #ccc;
    font-size: 14px;
    line-height: 20px;
    -webkit-align-self: center;
            align-self: center;
    height: 38px;
}

.cityInput {
    width: 250px;
}

.CitySearch {
    max-width: 300px;
    margin: 1rem auto auto auto;
    position: relative;
}

.suggestions {
    width: 250px;
    position: absolute;
    z-index: 2;
    -webkit-align-self: center;
            align-self: center;
    margin: 0;
    top: 43px;
    font-size: 14px;
    box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions>li {
    text-align: left;
    padding: 8px 6px 8px 10px;
    background-color: #eee;
    cursor: default;
}

.suggestions>li:last-child {
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
}

.suggestions>li:hover {
    background-color: #bcdcdb;
}

.EventList {
    width: 90vw;
    -webkit-align-self: center;
            align-self: center;
}

.event-container {
    padding: 10px 10px 35px;
    margin-bottom: 10px;
    text-align: left;
    border: 1px solid #ccc;
    border-radius: 4px;
    position: relative;
    text-align: center;
}

.details-btn {
    position: absolute;
    right: 5px;
    bottom: 5px;
    padding: 5px 8px;
    border-radius: 4px;
    outline: none;
    cursor: pointer;
}

.name {
    font-size: 18px;
    font-weight: 600;
}

.group-name {
    text-transform: uppercase;
    font-size: 14px;
}

.going, .visibility {
    font-style: italic;
}

.description {
    overflow-x: hidden;
}

.numEvents {
    display: -webkit-flex;
    display: flex;
    max-width: 300px;
    margin: 20px auto;
    text-align: center;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
}

.numInput {
    width: 50px;
    margin: 0 10px;
}

.button-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 20px;
    padding: 20px;
    font-family: sans-serif;
}

.Alert {
    position: fixed;
    top: -5px;
}

.offline {
    position: fixed;
    left: -2px;
    top: 2rem;
    -webkit-writing-mode: tb-rl;
        -ms-writing-mode: tb-rl;
            writing-mode: tb-rl;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}

.data-vis-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.data-vis-wrapper .recharts-responsive-container {
    min-width: 100%;
}

.recharts-surface {
  overflow: visible;
}

