@import url(https://fonts.googleapis.com/css?family=Roboto:500);
.App {
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.App * {
    box-sizing: border-box;
}

ul {
    padding: 0;
    list-style: none;
}

p {
    margin: 5px 0;
}

.display-none {
    display: none;
}

.showSuggestions {
    display: block;
}

input[type="text"], input[type="number"] {
    padding: 8px 6px 8px 10px;
    border-radius: 4px;
    outline: none;
    border: 1px solid #ccc;
    font-size: 14px;
    line-height: 20px;
    align-self: center;
    height: 38px;
}

.cityInput {
    width: 250px;
}

.CitySearch {
    max-width: 300px;
    margin: 1rem auto auto auto;
    position: relative;
}

.suggestions {
    width: 250px;
    position: absolute;
    z-index: 2;
    align-self: center;
    margin: 0;
    top: 43px;
    font-size: 14px;
    box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions>li {
    text-align: left;
    padding: 8px 6px 8px 10px;
    background-color: #eee;
    cursor: default;
}

.suggestions>li:last-child {
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
}

.suggestions>li:hover {
    background-color: #bcdcdb;
}

.EventList {
    width: 90vw;
    align-self: center;
}

.event-container {
    padding: 10px 10px 35px;
    margin-bottom: 10px;
    text-align: left;
    border: 1px solid #ccc;
    border-radius: 4px;
    position: relative;
    text-align: center;
}

.details-btn {
    position: absolute;
    right: 5px;
    bottom: 5px;
    padding: 5px 8px;
    border-radius: 4px;
    outline: none;
    cursor: pointer;
}

.name {
    font-size: 18px;
    font-weight: 600;
}

.group-name {
    text-transform: uppercase;
    font-size: 14px;
}

.going, .visibility {
    font-style: italic;
}

.description {
    overflow-x: hidden;
}

.numEvents {
    display: flex;
    max-width: 300px;
    margin: 20px auto;
    text-align: center;
    flex-direction: row;
    justify-content: center;
}

.numInput {
    width: 50px;
    margin: 0 10px;
}

.button-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 20px;
    padding: 20px;
    font-family: sans-serif;
}

.Alert {
    position: fixed;
    top: -5px;
}

.offline {
    position: fixed;
    left: -2px;
    top: 2rem;
    writing-mode: tb-rl;
    transform: rotate(180deg);
}

.data-vis-wrapper {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.data-vis-wrapper .recharts-responsive-container {
    min-width: 100%;
}

.recharts-surface {
  overflow: visible;
}

