@media only screen and (min-width: 992px) {
    .data-vis-wrapper {
        flex-direction: row;
    }
    .data-vis-wrapper .recharts-responsive-container {
        /* flex-basis: 50%; */
        flex-basis: auto;
        min-width: auto;
        margin: 0 auto;
    }
    .ResponsivePie {
        max-width: 400px;
    }
    .ResponsiveScatter {
        max-width: auto;
    }
    .EventList {
        width: 50vw;
    }
    .offline {
        left: 1rem;
        top: 1rem;
        writing-mode: inherit;
        transform: inherit;
    }
}